import React from 'react';
import { graphql } from 'gatsby';
import Link from 'gatsby-link';

import Layout from 'components/Layout';
import SEO from 'components/SEO';
import Container from 'components/ui/Container';
import TitleSection from 'components/ui/TitleSection';
import FormatHtml from 'components/utils/FormatHtml';

import * as Styled from './styles';

const Course = ({ data, pageContext }) => {
  const course = data.markdownRemark;

  return (
    <Layout>
      <SEO title={course.frontmatter.title} />
      <Container section>
        <TitleSection title={course.frontmatter.year} subtitle={course.frontmatter.title} />
        <FormatHtml content={course.html} />
      </Container>
    </Layout>
  );
};

export default Course;

export const query = graphql`
  query CourseBySlug($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        year
        tags
      }
    }
  }
`;
